@import "./scss/functions";


$themeColor: $primary !default;
$colors: ('white': $white, 'black': $black) !default;

@font-face {
  font-family: 'swiper-icons';
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

@import "./components/core/core";

.swiper-btn {
    width: 36px;
    height: 18px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 72px 36px;
    outline: 0;
    cursor: pointer;
    z-index: 1;

    &.swiper-btn-prev {
        background-image: $swiper-navigation-prev-icon;
    }

    &.swiper-btn-next {
        background-image: $swiper-navigation-next-icon;
    }

    @include focus(){
        box-shadow: $input-btn-focus-box-shadow;
    }
}

.swiper-button-disabled {
    opacity: .3;
    cursor: not-allowed;
}

.container {
    &.swiper-container {
        padding: inherit;
    }
}

.swiper-pagination {
    .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        background: currentColor; 
    }
}

.swiper-container:not(.swiper-container-horizontal) {
    > .swiper-pagination-bullets {
        .swiper-pagination-bullet {
            margin: 0 6px;
        }
    }
}

.swiper-container {
    &.swiper-scaled {
        .swiper-slide {
            transform: scale(.9);
            transition: transform .5s ease;
        
            &.swiper-slide-active {
                transform: scale(1);
            }
        }
    }
}

.swiper-dark {
    .swiper-btn {
        &.swiper-btn-prev {
            background-image: $swiper-navigation-prev-icon-dark;
        }
    
        &.swiper-btn-next {
            background-image: $swiper-navigation-next-icon-dark;
        }
    }
}

.swiper-thumbs {
    .swiper-slide {
        opacity: .5;

        &.swiper-slide-thumb-active {
            opacity: 1;
        }
    }
}